import { Component, TemplateRef, Input } from '@angular/core';

@Component({
    selector: 'zuknglib-vertical-items-presenter',
    template: `
        <div class="itemsContainer">
            <ng-container *ngFor="let i of items">
                <ng-container *ngTemplateOutlet="itemTemplate; context: { $implicit: i }"></ng-container>
            </ng-container>
        </div>
    `,
    styles: [".itemsContainer { float: left; height: 300px; display: flex; flex-direction: column; justify-content: center; padding:5px; } :host ::ng-deep .itemsContainer > * { margin: 5px; box-sizing: border-box; }"],
})
export class VerticalItemsPresenterComponent {
    @Input() items: any[];
    @Input() itemTemplate: TemplateRef<any>;
}
